import { useSelector, useDispatch } from 'react-redux';
import { selectError, selectLoggedInUser } from '../authSlice';
import { Link, Navigate } from 'react-router-dom';
import { loginUserAsync } from '../authSlice';
import { useForm } from 'react-hook-form';
import {
  MDBBtn,
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBInput
}
from 'mdb-react-ui-kit';


export default function Login() {
  const dispatch = useDispatch();
  const error = useSelector(selectError);
  const user = useSelector(selectLoggedInUser);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();


  return (
    <>
      {user && <Navigate to="/" replace={true}></Navigate>}
      <div style={{ marginLeft: "330px", marginTop: "-50px" }} >
        <MDBContainer className="my-5 gradient-form">

          <MDBRow>
            <MDBCol col='6' className="text-center mb-5 ml-20 ml-20">
              <div className="text-center p-3 my-5 ml-20 ml-20 d-flex flex-column w-50">
                <div className="text-center">
                  <img src="https://mdbcdn.b-cdn.net/img/Photos/new-templates/bootstrap-login-form/lotus.webp"
                    style={{ width: '185px', marginLeft: '130px' }} alt="logo" />
                  <h4 className="mt-1 mb-5 pb-1">We are The Space Cart Team</h4>
                </div>

                <p>Please login to your account</p>
                <form
                  noValidate
                  onSubmit={handleSubmit((data) => {
                    dispatch(
                      loginUserAsync({ email: data.email, password: data.password })
                    );
                  })}
                  className="space-y-6"
                >
                  <div>
                    <MDBInput wrapperClass='mb-4' label='Email address' id="email"
                      {...register('email', {
                        required: 'email is required',
                        pattern: {
                          value: /\b[\w\.-]+@[\w\.-]+\.\w{2,4}\b/gi,
                          message: 'email not valid',
                        },
                      })}
                      type="email" />
                    {errors.email && (
                      <p className="text-red-500">{errors.email.message}</p>
                    )}
                  </div>
                    <div className="flex items-center justify-between">
                      <div className="text-sm" style={{marginLeft:"390px",color:"black"}}>
                        <Link
                          to="/forgot-password"
                          className="font-semibold"
                        >
                          Forgot password?
                        </Link>
                      </div>
                    </div>
                    <div>
                      <div>
                        <MDBInput wrapperClass='mb-4' label='Password' id="password"
                          {...register('password', {
                            required: 'password is required',
                          })}
                          type="password" />
                        {errors.password && (
                          <p className="text-red-500">{errors.password.message}</p>
                        )}
                      </div>
                      {error && <p className="text-red-500">{error || error.message}</p>}
                    </div>

                    <div className="text-center pt-1 mb-5 pb-1">
                      <MDBBtn type="submit" className=" w-100 gradient-custom-2" style={{background:"linear-gradient(to right, #ee7724, #d8363a, #dd3675, #b44593)"}}>login</MDBBtn>
                    </div>
                </form>

                <div className="d-flex flex-row align-items-center justify-content-center pb-4 mb-4">
                  <p className="mb-0">Don't have an account?</p>
                  <Link to="/signup">
                    <MDBBtn outline className='mx-2' color='danger' style={{color:"linear-gradient(to right, #ee7724, #d8363a, #dd3675, #b44593)"}}>
                      Sign Up
                    </MDBBtn>
                  </Link>
                </div>

              </div>

            </MDBCol>
          </MDBRow>

        </MDBContainer>
      </div>
    </>
  );
}

